define('ember-orbit/links/link-proxy-mixin', ['exports'], function (exports) {

  'use strict';

  /**
   @module ember-orbit
   */

  var get = Ember.get,
      set = Ember.set;

  var LinkProxyMixin = Ember.Mixin.create({
    store: null,

    _ownerId: null,

    _ownerType: null,

    _linkField: null,

    reload: function() {
      var store = get(this, 'store');
      var promise = store.findLinked.call(store,
        get(this, '_ownerType'),
        get(this, '_ownerId'),
        get(this, '_linkField')
      );
      return promise;
    }
  });

  exports['default'] = LinkProxyMixin;

});