define('ember-orbit/fields/has-many', ['exports'], function (exports) {

  'use strict';

  /**
   @module ember-orbit
   */

  var hasMany = function(model, options) {
    options = options || {};
    options.type = 'hasMany';
    options.model = model;

    var meta = {
      options: options,
      isLink: true
    };

    return Ember.computed(function(key) {
      return this.getLinks(key);
    }).meta(meta).readOnly();
  };

  exports['default'] = hasMany;

});